
.option-beautifier {
  width: 100%;
  margin: 0;
  padding: 0;
}

.options-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  padding: 1rem 0;
}

.option-card {
  position: relative;
  background: linear-gradient(135deg, rgba(99, 102, 241, 0.1) 0%, rgba(236, 72, 153, 0.1) 100%);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 0.75rem;
  padding: 1rem 1.25rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(20px) saturate(180%);
  box-shadow:
    0 4px 16px rgba(0, 0, 0, 0.2),
    0 2px 4px rgba(99, 102, 241, 0.15),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  overflow: hidden;
  display: flex;
  align-items: center;
  gap: 1rem;

  // 渐变背景装饰
  background-image:
    radial-gradient(circle at 20% 30%, rgba(99, 102, 241, 0.15) 0%, transparent 50%),
    radial-gradient(circle at 80% 70%, rgba(236, 72, 153, 0.15) 0%, transparent 50%);

  // 装饰性渐变背景
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(
      90deg,
      transparent 0%,
      rgba(99, 102, 241, 0.6) 25%,
      rgba(236, 72, 153, 0.6) 75%,
      transparent 100%
    );
    opacity: 0;
    transition: opacity 0.3s ease;
  }

  // 闪烁效果
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent 0%, rgba(255, 255, 255, 0.1) 50%, transparent 100%);
    transition: left 0.5s ease;
  }

  &:hover {
    background: linear-gradient(135deg, rgba(99, 102, 241, 0.18) 0%, rgba(236, 72, 153, 0.18) 100%);
    background-image:
      radial-gradient(circle at 20% 30%, rgba(99, 102, 241, 0.25) 0%, transparent 50%),
      radial-gradient(circle at 80% 70%, rgba(236, 72, 153, 0.25) 0%, transparent 50%);
    border-color: rgba(236, 72, 153, 0.4);
    box-shadow:
      0 6px 24px rgba(0, 0, 0, 0.3),
      0 2px 8px rgba(99, 102, 241, 0.25),
      inset 0 1px 0 rgba(255, 255, 255, 0.15);
    transform: translateY(-2px);

    &::before {
      opacity: 1;
    }

    &::after {
      left: 100%;
    }

    .option-label {
      background: linear-gradient(135deg, #6366f1 0%, #ec4899 100%);
      transform: scale(1.05);
      box-shadow: 0 4px 12px rgba(99, 102, 241, 0.5);
    }

    .option-content {
      color: #f3f4f6;
    }

    .option-hint {
      opacity: 1;
      transform: translateY(0);
    }
  }

  &:active {
    transform: translateY(0);
    box-shadow:
      0 2px 8px rgba(0, 0, 0, 0.2),
      0 1px 2px rgba(99, 102, 241, 0.15);
  }
}

.option-label {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 2.25rem;
  height: 2.25rem;
  flex-shrink: 0;
  background: linear-gradient(135deg, rgba(99, 102, 241, 0.4) 0%, rgba(236, 72, 153, 0.4) 100%);
  border: 2px solid rgba(99, 102, 241, 0.5);
  border-radius: 50%;
  color: #ffffff;
  font-weight: 700;
  font-size: 1rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow:
    0 2px 8px rgba(99, 102, 241, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.2);
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

.option-content {
  flex: 1;
  color: #e5e7eb;
  font-size: 0.95rem;
  line-height: 1.6;
  text-align: left;
  word-wrap: break-word;
  transition: color 0.3s ease;
  letter-spacing: 0.01em;
}

.option-hint {
  display: none;
  align-items: center;
  gap: 0.5rem;
  color: rgba(236, 72, 153, 0.8);
  font-size: 0.8rem;
  font-weight: 500;
  opacity: 0;
  transform: translateY(10px);
  transition: all 0.3s ease;
  flex-shrink: 0;

  i {
    font-size: 0.85rem;
  }
}

.no-options {
  padding: 3rem 2rem;
  text-align: center;
  color: #9ca3af;
  font-size: 1rem;

  p {
    margin: 0;
  }
}

// 响应式设计
@media (max-width: 768px) {
  .options-container {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }

  .option-card {
    padding: 0.875rem 1rem;
  }

  .option-label {
    width: 2rem;
    height: 2rem;
    font-size: 0.9rem;
  }

  .option-content {
    font-size: 0.9rem;
  }
}
